/* -------------------------------------------------------------------
 * ## blog page header
 * ------------------------------------------------------------------- */

.page-header {
  background-color: #0d0a0b;
  background-repeat: no-repeat;
  background-position: center, center;
  padding-top: 20.4rem;
  padding-bottom: 7.2rem;
  position: relative;
  color: rgba(255, 255, 255, 0.5);
  min-height: 50.4rem;
}

.page-header--single {
  padding-top: 22.8rem;
  padding-bottom: 7.2rem;
  text-align: center;
}

.page-header::before {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.75;
  background-color: #0d0a0b;
}

.page-header::after {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  background: -moz-linear-gradient(
    top,
    transparent 0%,
    rgba(0, 0, 0, 0.65) 100%
  );
  /* FF3.6-15 */
  background: -webkit-linear-gradient(
    top,
    transparent 0%,
    rgba(0, 0, 0, 0.65) 100%
  );
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to bottom,
    transparent 0%,
    rgba(0, 0, 0, 0.65) 100%
  );
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#a6000000', GradientType=0);
  /* IE6-9 */
}

.page-header__content {
  font-family: "montserrat-regular", sans-serif;
  font-size: 2rem;
  line-height: 1.8;
